import { useNavigate } from "react-router-dom";

const AdminNavbar = () => {
    const navto = useNavigate()
    function eraseCookie(name) {
        document.cookie = name + '=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
    }
    function logout() {
        eraseCookie("token")
        navto("/login")

    }
    return (
        <div className="w3-top" style={{ zIndex: 5 }}>
            <div className="w3-large" style={{ backgroundColor: "#2b3238", width: "100%", height: 60 ,display:"flex",justifyContent:"space-between" }}>
                <div style={{ height: "100%", display: "flex", alignItems: "center", marginLeft: 5, color: "white" }}><i className="fa fa-pie-chart fa-fw w3-margin-right" />Admin Dashboard</div>
                <button onClick={logout} className="w3-button" style={{color:"white"}} > Logout</button >
            </div>
        </div>
    );
}
 
export default AdminNavbar;